.container{display:block;max-width:680px;width:80%;margin:120px auto}h1{color:#e91e63;font-size:48px;letter-spacing:-3px;text-align:center;margin:120px 0 80px 0;transition:.2s linear}.links{list-style-type:none}.links li{display:inline-block;margin:0 20px 0 0;transition:.2s linear}.links li.second{opacity:.6}.links li.second:hover{opacity:1}.links li.third{opacity:.6;float:right}.links li.third:hover{opacity:1}.links li a{text-decoration:none;color:#0f132a;font-weight:bolder;text-align:center;cursor:pointer}input,.signin__btn{border-radius:8px;border:none;font-family:"Montserrat",sans-serif}.input__block{margin:20px auto;display:block;position:relative}.input__block.first-input__block::before{content:"";position:absolute;top:-15px;left:50px;display:block;width:0;height:0;background:rgba(0,0,0,0);border-left:15px solid rgba(0,0,0,0);border-right:15px solid rgba(0,0,0,0);border-bottom:15px solid rgba(15,19,42,.1);transition:.2s linear}